export default {
	data() {
		return {
			 indicatorDots: true,
			 autoplay: true, // 轮播自动播放
			 interval: 5000, // 间隔多长时间滚动一次
			 duration: 500, // 滑动时间
			 dataList:[], // 新闻 列表
		}
	},
	onLoad() {
		// 调用新闻列表
		this.getNewsList()

	},
	methods: {
		// 新闻列表
		getNewsList(){
			uni.request({
				url: 'https://unidemo.dcloud.net.cn/api/news',
				method: 'GET',
				data: {},
				success: res => {
					console.log(res)
					this.dataList=res.data
				},
				fail: () => {},
				complete: () => {}
			});
		},
		// 跳转到详情页
		openInfo(e){
			var newsId=e.currentTarget.dataset.newsid
			uni.navigateTo({
				url:"../info/info?newsid="+newsId
			})
		}

	}
}
